<script lang="ts" context="module">
  import type { App, User } from 'app-interfaces';
  import { getContext, onMount, createEventDispatcher } from 'svelte';
  import { writable } from 'svelte/store';
  import { fly } from 'svelte/transition';
  import { Table } from 'sveltestrap';
  import { USER_ROLES } from '../bin/models/StaticTypes';
  import Details from '../Components/Details.svelte';
  import Add from '../Components/Add.svelte';
</script>

<script lang="ts">
  const {
    state: { view, users, subUsers, authenticated },
    Methods: { getUsers, addUser, updateUserId },
  } = getContext('state') as App;
  const dispatch = createEventDispatcher();
  const id = writable<string | null>(null);
  const open = writable(false);
  const add = writable(false);

  const handleDispatch = async (e: CustomEvent) => {
    const { data, callback } = e.detail;
    switch (e.type) {
      case 'add-user':
        try {
          const params = {
            name: data.name,
            email: data.email,
            password: data.password,
            role: data.role,
          };
          const newPost = await addUser(params);
          if (newPost) users.update((prevPosts) => [...prevPosts, newPost]);
          if ($add) add.update(() => false);
        } catch (error) {
          console.warn(error);
        }
        break;
      case 'update-user':
        try {
          const params = {
            id: data.id,
            name: data.name,
            email: data.email,
            role: data.role,
          };
          await updateUserId(params);
          callback();
        } catch (error) {
          console.warn(error);
        }
        break;

      default:
        break;
    }
  };

  const handleClick = (e: any) => id.update((n) => e.currentTarget.id);
  const actions = {
    async add(data: User) {
      const params = {
        name: data.name,
        email: data.email,
        password: data.password,
        role: data.role,
      };
      try {
        const newUser = await addUser(params);
        if (newUser) users.update((prevUsers) => ({ ...prevUsers, newUser }));
      } catch (error) {
        console.warn(error);
      }
      if (open) return id.update((n) => null);
    },
    async update(data: User) {
      const params = {
        id: data.id,
        name: data.name,
        email: data.email,
        role: data.role,
        password: data.password,
      };
      try {
        const newUser = await updateUserId(params);
        if (newUser) await handleClose();
      } catch (error) {
        console.warn(error);
      }
    },
  };
  async function handleClose() {
    try {
      await getUsers();
    } catch (error) {
      console.warn(error);
    }
    if (open) return id.update((n) => null);
  }
  $: {
    $id ? open.update((n) => true) : open.update((n) => false);
  }
  onMount(async () => {
    if (!$authenticated || $authenticated.role !== USER_ROLES.admin) {
      view.update((n) => '');
    }
    try {
      await getUsers();
    } catch (error) {
      console.warn(error);
    }
  });
</script>

<style>
  .table-container {
    max-height: 450px;
    overflow: auto;
  }
  .user-name {
    cursor: pointer;
    color: var(--primary);
  }
  .user-name:hover {
    text-decoration: underline;
    color: var(--dark);
  }
  .card {
    margin-bottom: 1rem;
    color: #3f3f3f;
    cursor: pointer;
  }
  .card.primary {
    color: var(--success);
    border: 1px solid var(--success);
    border-radius: 5px;
  }
  .card:hover {
    background-color: #3f3f3f;
    color: #ffffff;
  }

  @media (max-width: 640px) {
    .table-container {
      max-width: 450px;
    }
  }
</style>

<div class="container" in:fly={{ duration: 300, x: 300 }}>
  {#if $add && $authenticated && $authenticated.role === USER_ROLES.admin}
    <Add on:add-user={handleDispatch} props={{ option: 'users' }} />
  {/if}
  {#if $authenticated && $authenticated.role === USER_ROLES.admin}
    <div class="card primary" on:click={() => add.update((prev) => !prev)}>
      {#if $add}Cancel{:else}Add new User{/if}
    </div>
  {/if}
  <div class="table-container" in:fly={{ duration: 300, x: 300 }}>
    <Table striped hover bordered>
      <thead>
        <tr>
          <th>#</th>
          <th>Name</th>
          <th>Email</th>
          <th>Posts</th>
          <th>Role</th>
        </tr>
      </thead>
      <tbody>
        {#await $subUsers}
          <tr in:fly={{ duration: 300, x: 300 }}>
            <td colspan={5}>Loading users...</td>
          </tr>
        {:then result}
          {#each result.data.Users as user, i (i)}
            <tr in:fly={{ duration: 300, x: 300 }}>
              <th scope="row">{i + 1}</th>
              <td id={user['id']} class="user-name" on:click={handleClick}>
                {user['name']}
              </td>
              <td>{user['email']}</td>
              <td>{user['Posts'].length}</td>
              <td>{user['role']}</td>
            </tr>
          {:else}
            <tr in:fly={{ duration: 300, x: 300 }}>
              <td colspan={5}>No Users</td>
            </tr>
          {/each}
        {:catch error}
          <tr in:fly={{ duration: 300, x: 300 }}>
            <td colspan={5}>
              Error while loading users: {JSON.stringify(error)}
            </td>
          </tr>
        {/await}
      </tbody>
    </Table>
  </div>
  {#if $open}
    <Details
      on:update-user={handleDispatch}
      props={{ id: $id, toggle: handleClose, open: $open }} />
  {/if}
</div>
