<script lang="ts">
  import type { App, Post, User, Comment } from 'app-interfaces';
  import { getContext, createEventDispatcher } from 'svelte';
  import { fly } from 'svelte/transition';
  import { Label, FormGroup, Input, ButtonGroup } from 'sveltestrap';
  import { USER_ROLES } from '../bin/models/StaticTypes';

  export let props: {
    option: string;
    data?: Post | User | Partial<Comment>;
  };
  const dispatch = createEventDispatcher();
  const {
    state: { authenticated },
  } = getContext('state') as App;
  const handleSubmit = (e) => {
    const formData = new FormData(e.currentTarget);
    let params = {} as User | Post | Comment;
    formData.forEach((v, k) => Object.assign(params, { [k]: v }));
    switch (props.option) {
      case 'posts':
        dispatch('add-post', { data: params });
        break;
      case 'users':
        dispatch('add-user', { data: params });
        break;
      case 'comments':
        dispatch('add-comment', { data: params });
      default:
        break;
    }
  };
</script>

<style>
  .container {
    max-width: 450px;
    margin-bottom: 30px;
  }
</style>

<div class="container" transition:fly={{ duration: 300, y: -30 }}>
  <form id={`add-${props.option}`} on:submit|preventDefault={handleSubmit}>
    {#if props.option === 'users'}
      <FormGroup>
        <Label>Name</Label>
        <Input
          type="text"
          name="name"
          class="form-control"
          required
          size=""
          readonly={false} />
      </FormGroup>
      <FormGroup>
        <Label>Email</Label>
        <Input type="email" name="email" required size="" readonly={false} />
      </FormGroup>
      <FormGroup>
        <Label>Password</Label>
        <Input
          type="password"
          name="password"
          required
          size=""
          readonly={false} />
      </FormGroup>
      <FormGroup>
        <Label>Role</Label>
        <Input type="select" name="role" required size="" readonly={false}>
          <option value={USER_ROLES.user}>{USER_ROLES.user}</option>
          <option value={USER_ROLES.admin}>{USER_ROLES.admin}</option>
        </Input>
      </FormGroup>
    {:else if props.option === 'posts'}
      <FormGroup>
        <Label>Title</Label>
        <Input type="text" name="title" required size="" readonly={false} />
      </FormGroup>
      <FormGroup>
        <Label>Body</Label>
        <Input
          type="textarea"
          rows="5"
          bsSize="lg"
          name="body"
          size=""
          readonly={false} />
      </FormGroup>
      <FormGroup>
        <Label>Date</Label>
        <input type="date" name="date" class="form-control" />
      </FormGroup>
    {:else if props.option === 'comments'}
      <FormGroup>
        <textarea
          required
          rows={2}
          class="form-control"
          placeholder="Add a new comment..."
          bind:value={props.data['body']} />
      </FormGroup>
      {#if !$authenticated}
        <div class="d-grid-2">
          <FormGroup>
            <Label>Name:</Label>
            <input
              required
              type="text"
              class="form-control"
              placeholder="Enter your name"
              bind:value={props.data['name']} />
          </FormGroup>
          <FormGroup>
            <Label>Email:</Label>
            <input
              required
              type="email"
              class="form-control"
              placeholder="Enter your email"
              bind:value={props.data['email']} />
          </FormGroup>
        </div>
      {/if}
    {/if}
    <ButtonGroup>
      <button
        class="btn btn-outline-primary"
        type="submit"
        form={`add-${props.option}`}>Done</button>
    </ButtonGroup>
  </form>
</div>
