<script lang="ts">
  import type { App, Comment, Post, User } from 'app-interfaces';
  import { format } from 'date-fns';
  import { getContext, onMount } from 'svelte';
  import { writable } from 'svelte/store';
  import { fly } from 'svelte/transition';

  import PostModal from './Modals/PostDetails.svelte';
  import UserModal from './Modals/UserDetails.svelte';
  export let props: {
    id?: string;
    open: boolean;
    toggle: () => void;
  };
  const {
    state: { view },
    Methods: { getUserId, getPostId },
  } = getContext('state') as App;
  let data: Post | User;
  let loading = writable(false);
  let edit = writable(false);

  const modalProps = {
    transitionOptions: { fade: { duration: 300, delay: 300 } },
    size: 'lg',
    centered: true,
    backdrop: 'static' as 'static',
    isOpen: props.open,
    toggle: props.toggle,
  };
  onMount(async () => {
    if (!props.id) return;
    switch ($view) {
      case 'posts':
        try {
          if (!$loading) loading.update((n) => !n);
          data = await getPostId(props.id);
        } catch (error) {
          console.warn(error);
        }
        loading.update((n) => !n);
        break;
      case 'users':
        try {
          if (!$loading) loading.update((n) => !n);
          data = await getUserId(props.id);
        } catch (error) {
          console.warn(error);
        }
        loading.update((n) => !n);
        break;
      default:
        break;
    }
  });
</script>

<div class="container" in:fly={{ duration: 300, x: 30 }}>
  {#if $loading}
    Loading...
  {:else if $view === 'posts' && data && 'title' in data}
    <PostModal
      on:add-comment
      on:update-post
      props={{ modalProps, data: { ...data, date: format(new Date(data.date), 'yyyy-MM-dd') }, edit: $edit }} />
  {:else if $view === 'users' && data && 'name' in data}
    <UserModal on:update-user props={{ modalProps, data, edit: $edit }} />
  {/if}
</div>
